import {randomIntFromRange, randomColor, distance} from './utils'

const mouse = {
  x: innerWidth / 2,
  y: innerHeight / 2
}

const canvas = document.querySelector('canvas')
const ctx = canvas.getContext('2d')

canvas.width = innerWidth
canvas.height = innerHeight

const colors = ['#2185C5', '#7ECEFD', '#FFF6E5', '#FF7F66'];
let gravity = 1;
const friction = 0.80;

class Ball {
  constructor(x, y, addX, addY, radius, color) {
    this.x = x;
    this.y = y;
    this.addX = addX;
    this.addY = addY;
    this.radius = radius;
    this.color = color;
  }

  draw() {
    ctx.beginPath()
    ctx.arc(this.x, this.y, this.radius, 0, Math.PI * 2, false)
    ctx.fillStyle = this.color
    ctx.fill();
    ctx.strokeStyle = 'blue';
    ctx.stroke();
    ctx.closePath()
  }

  update() {
    if ((this.x + this.radius) > canvas.width || (this.x - this.radius) < 0) {
      this.addX = -this.addX;
    }
    if ((this.y + this.radius + this.addY) > canvas.height) {
      this.addY = -this.addY * friction;
    } else {
      this.addY += gravity;
    }
    this.x += this.addX;
    this.y += this.addY;
    this.draw()
  }
}

// Implementation

let balls;
function init() {
  balls = [];
  for (let i = 0; i < 190; i++) {
    const randomColor= colors[randomIntFromRange(0, colors.length)];
    const ballRadius = randomIntFromRange(14, 31);
    const randomXvelocity = randomIntFromRange(-2, 2);
    const randomX = randomIntFromRange(ballRadius, canvas.width - ballRadius);
    const randomY = randomIntFromRange(0, canvas.height - ballRadius);
    balls.push(new Ball(randomX, randomY, randomXvelocity, 2, ballRadius, randomColor));
  }
}

// Animation Loop
function animate() {
  requestAnimationFrame(animate)
  ctx.clearRect(0, 0, canvas.width, canvas.height)
  for (let i = 0; i < balls.length; i++) {
    balls[i].update();
  }
}

init();
animate();
document.addEventListener('click', function() {
  init();
})